import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class Navbar extends Component {
    render() {
        return (
            <nav className="navbar navbar-expand-lg navbar-light bg-white fixed-top py-0">
                <div className="container">
                    <Link to="/" className="text-decoration-none">
                        <div className="navbar-brand text-logo">FAFA</div>
                    </Link>
                    
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                    <div className="navbar-nav ms-auto">
                        <Link to="/" className="text-decoration-none nav-link active">
                            Home
                        </Link>
                        <Link to="/about" className="text-decoration-none nav-link">
                            About
                        </Link>
                        <Link to="/portofolio" className="text-decoration-none nav-link">
                            Portofolio
                        </Link>
                        <Link className="nav-link" to={{ pathname: "https://www.twitter.com/@24_fauzan"}} target="_blank">
                            Contact
                        </Link>
                    </div>
                    </div>
                </div>
            </nav>
        );
    }
}

export default Navbar;